/** dNEW **/
*{
  margin: 0;
  padding: 0;
  font-family: 'poppins',sans-serif;
  box-sizing: border-box;
}
:root{
  --body-color:#efefef;
  --nav-color: #1876f2;
  --bg-color: #fff;

}
.dark-theme{
  --body-color:#0a0a0a;
  --nav-color: #000;
  --bg-color: #000;

}
body{
  background: var(--body-color);
  transition: background 0.3s;



}
nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background:white;
  padding: 5px 5%;
  position: sticky;
  top: 0;
  z-index: 100;
  color: black;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);

}
.logo{
  width: 160px;
  margin-right: 45px;
  
}

.nav-right a{
  color:black;
  text-decoration: none;
}

.nav-left, .nav-right{
  display: flex;
  align-items: center;

}
.nav-left ul li{
  list-style: none;
  display: inline-block;

}
.nav-left ul li img{
  width: 28px;
  margin: 0 15px;

}
.nav-user-icon img{
  width: 40px;
  border-radius: 50%;
  cursor: pointer;
}
.nav-user-icon {
  margin-left: 30px;

}
.search-box{
  background: #efefef;
  width: 350px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  padding: 0 15px;

}
.search-box img{
  width: 18px;

}
.search-box input{
 
  background: transparent;
  padding: 10px;
  outline: none;
  border: 0;

}
.online{
  position: relative;
}
.online::after{
  content: '';
  width: 7px;
  height: 7px;
  border: 2px solid #fff;
  border-radius: 50%;
  background: #41db51;
  position: absolute;
  top: 0;
  right: 0;

}
.container{
  display: flex;
  justify-content: space-between;
  margin: 0px;
  padding: 7px 0;

}
.left-sidebar{
  flex-basis: 25%;
  position: sticky;
  z-index: 1;
  top: 70px;
  align-self: flex-start;
  background: var(--bg-color);
  padding: 20px;

  border: 0 !important;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 5px 15px;
}
.right-sidebar{
  flex-basis: 25%;
  position: sticky;
  top: 70px;
  align-self: flex-start;
  background: var(--bg-color);
  padding: 20px;

  color: #626262;
  border: 0 !important;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 5px 15px;

}
.main-content{
  flex-basis: 47%;
  border-radius: 17px;
  
}
.imp-links a, .shortcut-link a{
  text-decoration: none;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  color: #626262;
  width: fit-content;

}
.imp-links a img{
  width: 25px;
  margin-right: 15px;


}
.imp-links a:last-child{
  color:#1876f2;

}
.imp-links{
  border-bottom: 1px solid #ccc;
}
.shortcut-link a img{
  width: 40px;
  border-radius: 4px;
  margin-right: 15px;
}
.shortcut-link p{
  margin: 25px 0;
  color: #626262;
  font-weight: 500;


}

.event{
  display: flex;
  font-size: 16px;
  margin-bottom: 20px;

}
.left-event{
  border-radius: 10px;
  height: 65px;
  width: 65px;
  margin-right: 15px;
  padding-top: 10px;
  text-align:center;
  position: relative;
  overflow:hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

}
.event p{
  font-size: 12px;

}
.event a{
  font-size: 12px;
  text-decoration: none;
  color: #1876f2;

}
.left-event span{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #1876f2;
  color: #fff;
  font-size: 10px;
  padding: 4px 0;

}
.siderbar-ads{
  width: 100%;
  margin-bottom: 20px;
  border-radius: 4px;

}
.online-list{
  display: flex;
  align-items: center;
  margin-bottom: 20px;

}
.online-list .online img{
  width: 40px;
  border-radius: 50%;

}
.online-list .online{
  width: 40px;
  border-radius: 50%;
  margin-right: 15px;

}
.online-list .online::after{
  top: unset;
  bottom: 5px;

}
.story-gallery{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;

}
.story{
  flex-basis: 18%;
  padding-top: 32%;
  position: relative;
  background-position: center;
  background-size: cover;
  border-radius: 10px;

}
.story img{
  position: absolute;
  width: 45px;
  border-radius: 50%;
  top:10px;
  left: 10px;
  border: 5px solid #1876f2;

}
.story p{
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 14px;

}
.story1{
  
  background-image: linear-gradient(transparent,rgba(0,0,0,0.5)), url(https://i.postimg.cc/W4KfGPVh/status-1.png);
}
.story2{
  
  background-image: linear-gradient(transparent,rgba(0,0,0,0.5)), url(https://i.postimg.cc/x8GxVJzz/status-2.png);
}
.story3{
  
  background-image: linear-gradient(transparent,rgba(0,0,0,0.5)), url(https://i.postimg.cc/cCG9zMrW/status-3.png);
}
.story4{
  
  background-image: linear-gradient(transparent,rgba(0,0,0,0.5)), url(https://i.postimg.cc/3J7fCBsC/status-4.png);
}
.story5{
  
  background-image: linear-gradient(transparent,rgba(0,0,0,0.5)), url(https://i.postimg.cc/9Fp8x9V3/status-5.png);
}
.story.story1 img{
  top: unset;
  left: 50%;
  bottom: 40px;
  transform: translateX(-50%);
  border: 0;
  width: 35px;
}
.write-post-container{
  width: 100%;
  background: var(--bg-color);
  border-radius: 6px;
  padding: 20px;
  color: #626262;


}
.user-profile{
  display: flex;
  align-items: center;
  

}

.user-profile img{
  

}
.user-profile p{
  margin-bottom: -5px;
  font-weight: 500;
  color: #626262;
  margin-right: 10px;
  position: relative;
  left: 2%;
}
.user-profile small{
  font-size: 12px;
  
}
.post-input-container{
  padding-left: 55px;
  padding-top: 20px;

}
.post-input-container .input-text{
  width: 100%;
}
.post-input-container textarea{
  width: 100%;
  border: 0;
  outline: 0;
  border-bottom: 1px solid #ccc;
  background: transparent;
  resize: none;

}
.add-post-links{
  display: flex;
  margin-top: 10px;

}
.add-post-links a{
  text-decoration: none;
  display: flex;
  align-items: center;
  color: #626262;
  margin-right: 30px;
  font-size: 13px;

}
.add-post-links a img{
  width: 20px;
  margin-right: 10px;

}
.post-container{
  width: 100%;
  background: var(--bg-color);
  border-radius: 6px;
  padding: 20px;
  color: #626262;
  margin: 20px 0;

}
.user-profile span{
  font-size: 13px;
  color: #9a9a9a;
  margin-left: 10px;
}

.post-img{
  width: 100%;
  border-radius: 4px;
  margin-bottom: 5px;

}
.post-row{
  display: flex;
  align-items: center;
  justify-content: space-between;

}
.post-row-right{
  display: flex;
  flex-flow: row-reverse;
  position: relative;
  bottom: 35px;
}
.activity-icons div img{
  width: 18px;
  margin-right: 10px;

}
.activity-icons div{
  display: inline-flex;
  align-items: center;
  margin-right: 30px;

}
.post-porfile-icon{
  display: flex;
  align-items: center;


}
.post-porfile-icon img{
  width: 20px;
  border-radius: 50%;
  margin-right: 5px;

}
.post-row a{
  color: #9a9a9a;

}
.load-more-btn{
  display: block;
  margin: auto;
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid #9a9a9a;
  color: #626262;
  background: transparent;
  border-radius: 4px;

}
.footer{
  text-align: center;
  color: #9a9a9a;
  padding: 10px 0 20px;
  font-size: 13px;

}
.settings-menu{
  
  width: 90%;
  max-width: 350px;
  background: var(--bg-color);
  box-shadow: 0 0 10px rgba(0,0,0, 0.4);
  border-radius: 4px;
 
  
  right: 5%;
  max-height: 0;
  transition: max-height 0.3s;

}
.settings-menu-height{
  max-height: 450px;

}
.user-profile a{
  font-size: 12px;
  color: #1876f2;
  text-decoration: none;

}
.settings-menu-inner{
  padding: 20px;
}
.settings-menu hr{
  border: 0;
  height: 1px;
  background: #9a9a9a;
  margin: 15px 0;

}
.settings-links{
  display: flex;
  align-items: center;
  margin: 15px 0;

}
.settings-links .settings-icon{
  width: 38px;
  margin-right: 10px;
  border-radius: 50%;

}
.settings-links a{
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  color: #626262;
}
#dark-btn{
  position: absolute;
  top: 30px;
  right: 30px;
  background: #ccc;
  width: 45px;
  border-radius: 15px;
  padding: 10px 6px;
  cursor:pointer;
  display: flex;
  transition: padding-left 0.5s, background 0.5s;

}
#dark.btn span{
  width: 30px;
  height: 30px;
  background: #fff;
  border-radius: 60%;
  display: inline-flex;

}
#dark-btn.dark-btn-on{
  padding-left: 23px;
  background: #0a0a0a;

}
/*---------------Profile page-----------------*/
.pofile-container{
  padding: 20px 15%;
  color: #626262;

}
.cover-img{
  width: 100%;
  border-radius: 6px;
  margin-bottom: 14px;

}
.profile-details{
  background: var(--bg-color);
  padding: 20px;
  border-radius: 4px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

}
.pd-row{
  display: flex;
  align-items: flex-start;

}
.pd-image{
  width: 100px;
  margin-right: 20px;
  border-radius: 6px;

}
.pd-row div h3{
  font-size: 25px;
  font-weight: 600;

}
.pd-row div p{
  font-size: 13px;

}
.pd-row div img{
  width: 25px;
  border-radius: 50%;
  margin-top: 12px;

}
.pd-right button{
  background: #1876f2;
  border: 0;
  outline: 0;
  padding: 6px 10px;
  display: inline-flex;
  align-items: center;
  color: #fff;
  border-radius: 3px;
  margin-left: 10px;
  cursor: pointer;

}
.pd-right button img{
  height: 15px;
  margin-right: 10px;

}
.pd-right button:first-child{
  background: #e4e6eb;
  color: #000;

}
.pd-right{
  text-align: right;

}
.pd-right a{
  background: #e4e6eb;
  border-radius: 3px;
  padding: 12px;
  display: inline-flex;
  margin-top: 30px;

}
.pd-right a  img{
  width: 20px;

}
.profile-info{
  display: flex;
  align-self: flex-start;
  justify-content: space-between;
  margin-top: 20px;

}
.info-col{
  flex-basis: 33%;

}
.post-col{
  flex-basis: 65%;

}
.profile-intro{
  background: var(--bg-color);
  padding: 20px;
  margin-right: 20px;
  border-radius: 4px;

}
.profile-info h3{
  font-weight: 600;
}
.intro-text{
  text-align: center;
  margin: 10px 0;
  font-size: 15px;

}
.intro-text img{
  width: 15px;
  margin-bottom: -3px;

}
.profile-intro hr{
  border: 0;
  height: 1px;
  background: #ccc;
  margin: 24px 0;

}
.profile-info ul li{
  list-style: none;
  font-size: 15px;
  margin: 15px 0;
  display: flex;
  align-items: center;

}
.profile-info ul li img{
  width: 26px;
  margin-right: 10px;

}
.title-box{
  display: flex;
  align-items: center;
  justify-content: space-between;

}
.title-box a{
  text-decoration: none;
  color: #1876f2;
  font-size: 14px;

}
.photo-box{
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  margin-top: 15px;

}
.photo-box div img{
  width: 100%;
  cursor: pointer;

}
.profile-intro p{
  font-size: 14px;

}
.friends-box{
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  margin-top: 15px;

}
.friends-box div img{
  width: 100%;
  cursor: pointer;
  padding-bottom: 20px;
  
}
.friends-box div{
  position: relative;
}
.friends-box p{
  position: absolute;
  bottom: 0;
  left: 0;

}
/*----------------- Media Query For Home Page -----------------*/

@media (max-width: 900px){
  .right-sidebar{
      display: none;
  }
  .left-sidebar{
      display: none;

  }
  nav{
      flex-wrap: wrap;
  }
  .search-box{
      display: none;
  }
  .logo{
      width: 110px;
      margin-right: 20px;
  }
  .nav-left ul li img{
      width: 20px;
      margin: 0 8px;

  }
  .nav-user-icon img{
      width: 30px;

  }
  .nav-user-icon{
      margin-left: 0;

  }
  .add-post-links{
      flex-wrap: wrap;

  }
  .main-content{
      flex-basis: 100%;
      border-radius: 17px;
  }
  .story p{
      font-size: 10px;

  }
  .story.story1 img{
      width:  25px;
      bottom: 30px;

  }
  .add-post-links a{
      margin-right: 12px;
      font-size: 9px;

  }
  .add-post-links a img{
      width: 16px;
      margin-right: 5px;
  }
  .post-input-container{
      padding-left: 0;

  }

}

/*=========================modal===========================================*/


.modalContainer {
  max-width: 600px;
  width: 100%;
  position: fixed;
  height: 600px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  background-color: #ffffff;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.75);
  border-radius: 8px;
  z-index: 1000;
  
}
/*=========================modal end=======================================*/

/*----------------------- Media Query For Profile Page------------------------*/
@media (max-width: 900px){
  .profile-container{
      padding: 20px 5%;

  }
  .profile-details{
      flex-wrap: wrap;

  }
  .pd-right{
      text-align: left;
      margin-top: 15px;

  }
  .pd-right button{
      margin-left: 0;
      margin-right: 10px;

  }
  .pd-row div h3{
      font-size: 16px;

  }
  .profile-info{
      flex-wrap: wrap;

  }
  .info-col, .post-col{
      flex-basis: 100%;

  }

}